import { useQuery } from "@tanstack/react-query"
import { DocumentsCollectionAPIResponse } from "../documents.types"
import { loadDocumentsCollectionById } from "../services/loadDocumentsCollectionById"

type UseDocumentsCollectionInfoProps = {
  collectionId: string
}

const documentsCollectionQueryKeys = {
  documentsCollection: (collectionId: string | undefined) =>
    ["documentsCollection", collectionId] as const,
}

const useDocumentsCollectionInfo = ({ collectionId }: UseDocumentsCollectionInfoProps) => {
  return useQuery<DocumentsCollectionAPIResponse>({
    queryKey: documentsCollectionQueryKeys.documentsCollection(collectionId),
    queryFn: () => loadDocumentsCollectionById(collectionId),
  })
}

export { useDocumentsCollectionInfo }
export type { UseDocumentsCollectionInfoProps }
